// app/admin/products/ProductForm.tsx
'use client';

import { useState, useEffect } from 'react';

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL!;

export type ProductFormValues = {
    name: string;
    price: number;
    stock: number;
    thumbnailUrl: string;
    description: string;
    cid: number | null;
};

type ProductFormProps = {
    mode: 'create' | 'edit';
    initialValues?: Partial<ProductFormValues>;
    submitting: boolean;
    error: string | null;
    onSubmit: (values: ProductFormValues) => void;
    onCancel: () => void;
};

type CategoryOption = {
    cid: number;
    name: string;
};

export default function ProductForm({
    mode,
    initialValues,
    submitting,
    error,
    onSubmit,
    onCancel,
}: ProductFormProps) {
    const [name, setName] = useState('');
    const [price, setPrice] = useState('');
    const [stock, setStock] = useState('');
    const [thumbnailUrl, setThumbnailUrl] = useState('');
    const [description, setDescription] = useState('');

    const [cid, setCid] = useState('');
    const [categories, setCategories] = useState<CategoryOption[]>([]);
    const [catLoading, setCatLoading] = useState(true);

    // Ï¥àÍ∏∞Í∞í ÏÑ∏ÌåÖ (ÏàòÏ†ï Î™®ÎìúÏùº Îïå Í∏∞Ï°¥ Îç∞Ïù¥ÌÑ∞ Îì§Ïñ¥Ïò§Î©¥ ÌèºÏóê ÏÑ∏ÌåÖ)
    useEffect(() => {
        if (initialValues) {
            if (initialValues.name) setName(initialValues.name);
            if (typeof initialValues.price === 'number') setPrice(String(initialValues.price));
            if (typeof initialValues.stock === 'number') setStock(String(initialValues.stock));
            if (typeof initialValues.thumbnailUrl === 'string') setThumbnailUrl(initialValues.thumbnailUrl);
            if (initialValues.description) setDescription(initialValues.description);
            if (typeof initialValues.cid === 'number') setCid(String(initialValues.cid));
        }
    }, [initialValues]);

    useEffect(() => {
        const fetchCategories = async () => {
            try {
                const res = await fetch(`${API_BASE_URL}/categories`);
                if (!res.ok) throw new Error('Ïπ¥ÌÖåÍ≥†Î¶¨ Ï°∞Ìöå Ïã§Ìå®');
                const data: CategoryOption[] = await res.json();
                setCategories(data);
            } catch (e) {
                console.error('Ïπ¥ÌÖåÍ≥†Î¶¨ Î™©Î°ù Ï°∞Ìöå Ïã§Ìå®', e);
            } finally {
                setCatLoading(false);
            }
        };

        fetchCategories();
    }, []);

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();

        // ÌîÑÎ°†Ìä∏ Îã® Í≤ÄÏ¶ù
        if (!name.trim()) {
            alert('ÏÉÅÌíàÎ™ÖÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî.');
            return;
        }
        if (price.trim() === '') {
            alert('Í∞ÄÍ≤©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî.');
            return;
        }
        if (stock.trim() === '') {
            alert('Ïû¨Í≥†Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.');
            return;
        }
        if (!description.trim()) {
            alert('ÏÉÅÌíà ÏÑ§Î™ÖÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî.');
            return;
        }

        const priceNum = Number(price);
        const stockNum = Number(stock);

        if (!Number.isFinite(priceNum) || priceNum < 0) {
            alert('Í∞ÄÍ≤©ÏùÄ 0 Ïù¥ÏÉÅÏùò Ïà´ÏûêÎ°ú ÏûÖÎ†•ÌïòÏÑ∏Ïöî.');
            return;
        }
        if (!Number.isFinite(stockNum) || stockNum < 0) {
            alert('Ïû¨Í≥†Îäî 0 Ïù¥ÏÉÅÏùò Ïà´ÏûêÎ°ú ÏûÖÎ†•ÌïòÏÑ∏Ïöî.');
            return;
        }

        let cidNum: number | null = null;
        if (cid) {
            const parsed = Number(cid);
            if (!Number.isNaN(parsed)) {
                cidNum = parsed;
            }
        }

        onSubmit({
            name: name.trim(),
            price: priceNum,
            stock: stockNum,
            thumbnailUrl: thumbnailUrl.trim(),
            description: description.trim(),
            cid: cidNum,
        });
    };

    return (
        <section className="bg-white rounded-2xl shadow-sm border border-slate-100 p-6 max-w-3xl">
            <form className="space-y-5" onSubmit={handleSubmit}>
                {/* Í∏∞Î≥∏ Ï†ïÎ≥¥ Í∑∏Î¶¨Îìú */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {/* ÏÉÅÌíàÎ™Ö */}
                    <div className="flex flex-col gap-1">
                        <label className="text-xs font-medium text-slate-600">
                            ÏÉÅÌíàÎ™Ö <span className="text-rose-500">*</span>
                        </label>
                        <input
                            type="text"
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            placeholder="Ïòà: Î¨¥Ï†ëÏ†ê ÌÇ§Î≥¥Îìú 87ÌÇ§"
                            className="h-11 w-full rounded-lg border border-slate-200 px-3 text-sm outline-none focus:border-indigo-500 focus:ring-1 focus:ring-indigo-500"
                        />
                    </div>

                    {/* Í∞ÄÍ≤© */}
                    <div className="flex flex-col gap-1">
                        <label className="text-xs font-medium text-slate-600">
                            Í∞ÄÍ≤©(Ïõê) <span className="text-rose-500">*</span>
                        </label>
                        <input
                            type="number"
                            min={0}
                            value={price}
                            onChange={(e) => setPrice(e.target.value)}
                            placeholder="Ïòà: 19900"
                            className="h-11 w-full rounded-lg border border-slate-200 px-3 text-sm outline-none focus:border-indigo-500 focus:ring-1 focus:ring-indigo-500"
                        />
                    </div>

                    {/* Ïû¨Í≥† */}
                    <div className="flex flex-col gap-1">
                        <label className="text-xs font-medium text-slate-600">
                            Ïû¨Í≥† ÏàòÎüâ <span className="text-rose-500">*</span>
                        </label>
                        <input
                            type="number"
                            min={0}
                            value={stock}
                            onChange={(e) => setStock(e.target.value)}
                            placeholder="Ïòà: 10"
                            className="h-11 w-full rounded-lg border border-slate-200 px-3 text-sm outline-none focus:border-indigo-500 focus:ring-1 focus:ring-indigo-500"
                        />
                    </div>

                    {/* Ïç∏ÎÑ§Ïùº URL */}
                    <div className="flex flex-col gap-1">
                        <label className="text-xs font-medium text-slate-600">
                            Ïç∏ÎÑ§Ïùº Ïù¥ÎØ∏ÏßÄ URL
                        </label>
                        <input
                            type="text"
                            value={thumbnailUrl}
                            onChange={(e) => setThumbnailUrl(e.target.value)}
                            placeholder="https://example.com/image.jpg"
                            className="h-11 w-full rounded-lg border border-slate-200 px-3 text-sm outline-none focus:border-indigo-500 focus:ring-1 focus:ring-indigo-500"
                        />
                        <p className="text-[11px] text-slate-400">
                            Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú ÎåÄÏã† URLÎßå ÏÇ¨Ïö©Ìï©ÎãàÎã§.
                        </p>
                    </div>

                    {/* üîπ Ïπ¥ÌÖåÍ≥†Î¶¨ ÏÑ†ÌÉù */}
                    <div className="flex flex-col gap-1">
                        <label className="text-xs font-medium text-slate-600">
                            Ïπ¥ÌÖåÍ≥†Î¶¨
                        </label>
                        <select
                            value={cid}
                            onChange={(e) => setCid(e.target.value)}
                            className="h-11 w-full rounded-lg border border-slate-200 px-3 text-sm outline-none focus:border-indigo-500 focus:ring-1 focus:ring-indigo-500 bg-white"
                        >
                            <option value="">ÏÑ†ÌÉù Ïïà Ìï®</option>
                            {categories.map((c) => (
                                <option key={c.cid} value={c.cid}>
                                    {c.name}
                                </option>
                            ))}
                        </select>
                        {catLoading && (
                            <p className="text-[11px] text-slate-400">
                                Ïπ¥ÌÖåÍ≥†Î¶¨Î•º Î∂àÎü¨Ïò§Îäî Ï§ëÏûÖÎãàÎã§...
                            </p>
                        )}
                    </div>
                </div>

                {/* ÏÑ§Î™Ö */}
                <div className="flex flex-col gap-1">
                    <label className="text-xs font-medium text-slate-600">
                        ÏÉÅÌíà ÏÑ§Î™Ö <span className="text-rose-500">*</span>
                    </label>
                    <textarea
                        value={description}
                        onChange={(e) => setDescription(e.target.value)}
                        rows={6}
                        placeholder="ÏÉÅÌíà ÏÉÅÏÑ∏ ÏÑ§Î™ÖÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî."
                        className="w-full rounded-lg border border-slate-200 px-3 py-2 text-sm outline-none focus:border-indigo-500 focus:ring-1 focus:ring-indigo-500"
                    />
                </div>

                {/* ÏóêÎü¨ Î©îÏãúÏßÄ */}
                {error && (
                    <p className="text-xs text-rose-600">
                        {error}
                    </p>
                )}

                {/* Î≤ÑÌäº ÏòÅÏó≠ */}
                <div className="flex items-center justify-end gap-2">
                    <button
                        type="button"
                        onClick={onCancel}
                        className="rounded-lg border border-slate-300 px-4 py-2 text-xs text-slate-700 hover:bg-slate-50"
                        disabled={submitting}
                    >
                        Ï∑®ÏÜå
                    </button>
                    <button
                        type="submit"
                        disabled={submitting}
                        className="rounded-lg bg-indigo-600 px-5 py-2 text-xs font-medium text-white hover:bg-indigo-700 disabled:opacity-50 disabled:cursor-not-allowed"
                    >
                        {submitting
                            ? mode === 'create'
                                ? 'Îì±Î°ù Ï§ë...'
                                : 'ÏàòÏ†ï Ï§ë...'
                            : mode === 'create'
                                ? 'ÏÉÅÌíà Îì±Î°ù'
                                : 'ÏÉÅÌíà ÏàòÏ†ï'}
                    </button>
                </div>
            </form>
        </section>
    );
}
