'use client';

import { useEffect, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import ProductForm, { ProductFormValues } from '../../ProductForm';

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL!;

type Product = {
    pid: number;
    name: string;
    description: string;
    price: number;
    stock: number;
    thumbnailUrl?: string | null;
    createdAt: string;
    cid?: number | null;
};

export default function AdminProductEditPage() {
    const params = useParams();
    const router = useRouter();
    const pid = Number(params?.pid);

    const [loading, setLoading] = useState(true);
    const [initialProduct, setInitialProduct] = useState<Product | null>(null);
    const [submitting, setSubmitting] = useState(false);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        if (!pid || Number.isNaN(pid)) {
            setError('ÏûòÎ™ªÎêú ÏÉÅÌíà Î≤àÌò∏ÏûÖÎãàÎã§.');
            setLoading(false);
            return;
        }

        const fetchProduct = async () => {
            try {
                const res = await fetch(`${API_BASE_URL}/products/${pid}`);
                if (res.status === 404) {
                    setError('Ìï¥Îãπ ÏÉÅÌíàÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.');
                    setInitialProduct(null);
                    return;
                }
                if (!res.ok) throw new Error('ÏÉÅÌíà Ï°∞ÌöåÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');

                const data: Product = await res.json();
                setInitialProduct(data);
            } catch (e: any) {
                setError(e.message ?? 'ÏÉÅÌíà Ï°∞Ìöå Ï§ë Ïò§Î•ò Î∞úÏÉù');
            } finally {
                setLoading(false);
            }
        };

        fetchProduct();
    }, [pid]);

    const handleSubmit = async (values: ProductFormValues) => {
        setSubmitting(true);
        setError(null);

        try {
            const res = await fetch(`${API_BASE_URL}/products/${pid}`, {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(values),
            });

            if (!res.ok) {
                const text = await res.text();
                console.error('Update failed:', text);
                throw new Error('ÏÉÅÌíà ÏàòÏ†ïÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
            }

            router.push(`/admin/products/${pid}`);
        } catch (e: any) {
            setError(e.message ?? 'ÏÉÅÌíà ÏàòÏ†ï Ï§ë Ïò§Î•ò Î∞úÏÉù');
        } finally {
            setSubmitting(false);
        }
    };

    if (loading) {
        return (
            <div className="py-10 text-center text-sm text-slate-500">
                ÏÉÅÌíà Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Ï§ëÏûÖÎãàÎã§...
            </div>
        );
    }

    if (!initialProduct) {
        return (
            <div className="space-y-4 p-6">
                <h2 className="text-lg font-semibold">ÏÉÅÌíà ÏàòÏ†ï</h2>
                <p className="text-sm text-rose-600">
                    {error ?? 'ÏÉÅÌíà Ï†ïÎ≥¥Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.'}
                </p>
                <button
                    type="button"
                    onClick={() => router.push('/admin/products')}
                    className="rounded-md border border-slate-300 px-4 py-2 text-xs text-slate-700 hover:bg-slate-50"
                >
                    Î™©Î°ùÏúºÎ°ú
                </button>
            </div>
        );
    }

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <div>
                    <h2 className="text-lg font-semibold">ÏÉÅÌíà ÏàòÏ†ï</h2>
                    <p className="text-xs text-slate-500">
                        ÏÉÅÌíà Ï†ïÎ≥¥Î•º ÏàòÏ†ïÌï©ÎãàÎã§.
                    </p>
                </div>
                <button
                    type="button"
                    onClick={() => router.push('/admin/products')}
                    className="rounded-full border border-slate-300 px-4 py-2 text-xs text-slate-700 hover:bg-slate-50"
                >
                    Î™©Î°ùÏúºÎ°ú
                </button>
            </div>

            <ProductForm
                mode="edit"
                submitting={submitting}
                error={error}
                initialValues={{
                    name: initialProduct.name,
                    description: initialProduct.description,
                    price: initialProduct.price,
                    stock: initialProduct.stock,
                    thumbnailUrl: initialProduct.thumbnailUrl ?? '',
                    cid: initialProduct.cid ?? null,
                }}
                onSubmit={handleSubmit}
                onCancel={() => router.push(`/admin/products/${pid}`)}
            />
        </div>
    );
}